<template>

  <h6 v-show="data.length == 0">
    <div class="mt-5"> No data Available</div>
  </h6>

  <div v-for="(item, index) in data" :key="index" @drop="$emit('onDrop', index)" @dragstart="$emit('startDrag', index)"
    draggable="true" class="input-group d-flex">

    <div class="input-group-prepend card" :class="{ darkItems: !isLight }">
      <div class="input-group-text" :class="{ darkItems: !isLight }">
        <div class="round" :class="{ darkItems: !isLight }">
          <input @click="$emit('checked', index)" :class="{ darkItems: !isLight }" type="checkbox" :checked="item.status"
            :id="index" />
          <label :for="index" :class="{ darkItems: !isLight }"></label>
        </div>
      </div>
    </div>
    <div class="input-group-text form-control" v-bind:class="{ done: item.status, darkItems: !isLight }">
      {{ item.name }}
    </div>
    <div class="times input-group-prepend card" :class="{ darkItems: !isLight }">
      <span @click="$emit('del', index)">&times;</span>
    </div>

  </div>

</template>
<script>
export default {
  name: 'To-do-list',
  props: {
    data: Array,
    isLight: Boolean
  },
  emits: ["checked", "del", "startDrag", "onDrop"],
}
</script>
<style>
.times {
  margin-right: 10px;
  cursor: pointer;
  display: block;
  padding: 0px;
  width: 30px;
  height: 30px;
  position: inherit;
  font-size: 30px;

}

.times>span {

  display: none;
  padding: 0px;
  margin-top: 0px;
  position: relative;

}

.times:hover>span {
  display: block;
  padding: 0px;

}

.done {
  text-decoration: line-through;
  color: grey;
}
</style>
