

let dataList = []


export const List = {
   addToList: ({name, status}) => {
       dataList.push({ name, status })
       return dataList
   },
   removeFromList: ({id}) => {
      dataList.splice(id, 1)
      return dataList
   },
   getAllList: () => {
      return dataList

   },
   changeStatus:({id})=>{
     dataList[id].status=!dataList[id].status
     return dataList
   },
   getCompleted:()=>{
      return dataList.filter(item =>item.status ==true)
   },
   getActive:()=>{
      return dataList.filter(item => item.status ==false)
   },
   clearCompleted:()=>{
      dataList = List.getActive()
      return dataList
   },
   changeArrayPosition:(fromIndex,toIndex)=>{
      var movingItem = dataList[fromIndex];
      dataList.splice(fromIndex, 1);
      dataList.splice(toIndex, 0, movingItem);
      return dataList
   }
}