<template>
  <div id="header">
    <div class="headerItems">
      <div>
        <h1>{{ msg }}</h1>
      </div>
      <div @click="$emit('toggle')" class="icon" v-bind:class="{ moon: isLight, sun: !isLight }"></div>
    </div>
  </div>

  <div class="cards">
    <form :class="{ darkItems: !isLight }" @submit.prevent="addTodo">
      <div class="input-group input" :class="{ darkItems: !isLight }">

        <div class="input-group-prepend card" :class="{ darkItems: !isLight }">
          <div class="input-group-text">
            <div class="round">
              <input type="checkbox" checked id="checkbox" />
              <label for="checkbox"></label>
            </div>
          </div>
        </div>
        <input type="text" v-model="input" class="form-control shadow-none" :class="{ darkItems: !isLight }"
          placeholder="Create a new todo...">

      </div>
    </form>
    <div class="card text-center mt-2 section" :class="{ darkItems: !isLight }">

      <div @dragover.prevent @dragenter.prevent class="card-body w-100">
        <TodoList @startDrag="startDrag($event, index)" @onDrop="onDrop($event, index)" @del="deleteTodo($event, index)"
          @checked="handleCheckBox($event, index)" v-bind:data="todo" v-bind:isLight="isLight" />
      </div>
      <div class="card-footer text-muted" :class="{ darkItems: !isLight }">
        <div>
          <span>{{ fullData.filter(item => item.status == false).length }}&nbsp;items left</span>
        </div>
        <div class="hide">
          <button type="button" @click="getAll()" class="btn btn-link">All</button>
          <button type="button" @click="getActive()" class="btn btn-link">Active</button>
          <button type="button" @click="getCompleted()" class="btn btn-link">Completed</button>
        </div>
        <div>
          <button type="button" @click="clearCompleted()" class="btn btn-link">Clear completed</button>
        </div>
      </div>

    </div>
    <form :class="{ darkItems: !isLight }" @submit.prevent="addTodo">
      <div class="input-group input footer mt-2" :class="{ darkItems: !isLight }">
        <div>
          <button type="button" @click="getAll()" class="btn btn-link">All</button>
          <button type="button" @click="getActive()" class="btn btn-link">Active</button>
          <button type="button" @click="getCompleted()" class="btn btn-link">Completed</button>
        </div>

      </div>
    </form>


    <br />
    <small>Drag and drop to reorder list</small>

  </div>
</template>

<script>
import TodoList from './list.vue'
import { List as listFunctions } from './list.helper';
export default {
  name: 'To-do',
  props: {
    msg: String,
    isLight: Boolean
  },
  emits: ['toggle'],

  components: {
    TodoList
  },
  data: () => {
    return {
      input: "",
      startINdex: null,
      todo: listFunctions.getAllList(),
      fullData: listFunctions.getAllList()
    }
  },
  methods: {

    addTodo() {
      let result = [...listFunctions.addToList({
        name: this.input,
        status: false
      })]
      this.todo = result
      this.fullData = result
      this.input = ''

    },
    getCompleted() {
      this.todo = [...listFunctions.getCompleted()]
    },
    deleteTodo(index) {
      let result = [...listFunctions.removeFromList({ id: index })]
      this.todo = result
      this.fullData = result

    },
    getActive() {
      this.todo = [...listFunctions.getActive()]
    },
    getAll() {
      this.todo = [...listFunctions.getAllList()]
    },
    clearCompleted() {
      this.todo = [...listFunctions.clearCompleted()]
    },
    handleCheckBox(index) {
      this.todo = [...listFunctions.changeStatus({ id: index })]
    },
    startDrag(index) {
      this.startINdex = index
    },
    onDrop(index) {
      this.todo = [...listFunctions.changeArrayPosition(this.startINdex, index)]
      this.startINdex = null
    },


  }
}
</script>


