<template >


  <Todo @toggle="toggleMode()" v-bind:isLight="isLight" msg="TODO" />

</template>

<script>
import Todo from './components/todo/Todo.vue'

export default {
  name: 'App',
  components: {
    Todo
  },
  data: () => {
    return {
      isLight: true
    }
  },
  methods: {
    toggleMode() {
      this.isLight = !this.isLight
      document.getElementById('app').classList.toggle('dark')

    }
  }
}
